<script setup lang="ts">
import { ref } from 'vue'
import { ChevronDown, ChevronUp } from 'lucide-vue-next'

const faqs = [
  {
    question: 'Can I customize the look and feel of the checkout?',
    answer: 'Yes, you can fully customize colors, fonts, and logos in the Dashboard Studio.'
  },
  {
    question: 'How do I handle refunds?',
    answer: 'Refunds can be initiated directly from the Management -> Orders section in the dashboard.'
  },
  {
    question: 'Is the SDK compatible with React/Vue/Angular?',
    answer: 'Yes, the SDK is framework-agnostic and works with any JavaScript framework.'
  },
  {
    question: 'What payment methods are supported?',
    answer: 'We support over 100 global payment methods including Credit Cards, PayPal, Apple Pay, and Google Pay.'
  },
  {
    question: 'How secure is the integration?',
    answer: 'Appcharge is PCI-DSS Level 1 certified. We handle all sensitive payment data.'
  }
]

const openIndex = ref<number | null>(0)

const toggle = (index: number) => {
  openIndex.value = openIndex.value === index ? null : index
}
</script>

<template>
  <div class="h-full w-full flex items-center justify-center bg-white px-8">
    <div class="max-w-4xl w-full">
      <h2 class="text-4xl font-bold text-slate-900 mb-12 text-center">Frequently Asked Questions</h2>
      
      <div class="space-y-4">
        <div 
          v-for="(faq, index) in faqs" 
          :key="index"
          class="border border-slate-200 rounded-xl overflow-hidden transition-all duration-300"
          :class="openIndex === index ? 'bg-blue-50 border-blue-200 shadow-md' : 'bg-white hover:border-blue-300'"
        >
          <button 
            @click="toggle(index)"
            class="w-full px-6 py-4 flex items-center justify-between text-left focus:outline-none"
          >
            <span class="font-bold text-lg text-slate-800">{{ faq.question }}</span>
            <component 
              :is="openIndex === index ? ChevronUp : ChevronDown" 
              class="w-5 h-5 text-slate-500"
            />
          </button>
          
          <div 
            v-show="openIndex === index"
            class="px-6 pb-6 pt-0 text-slate-600 leading-relaxed"
          >
            {{ faq.answer }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
